/* SLIDER */
#slider {
    position: relative;
    grid-template-columns: 1fr 1fr;
    padding: 0 !important;
    .slider-lp {
        display: grid;
        h2 {
            position: absolute;
            // left: 50%
            top: 40%;
            color: #ffffff;
            margin: 0;
            width: 100%;
            text-align: center;
            opacity: 0;
            @include translate(0%,-50%);
            text-shadow: 0 2px 10px rgba(0,0,0,0.4); } }
